	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/03/20000312007.jpg\" height=\"262\" width=\"350\" border=\"0\" alt=\"crh0317.jpg: \" /></p>\n<p>It's the usual story. It's the weekend, and Christopher is in a much better mood than either of us. It's 8:30am, he's just had breakfast, and he's babbling and gurgling happily at his Fisher Price Teddy Bear mobile. </p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/03/crh.jpg\" height=\"90\" width=\"87\" align=\"right\" /> He laughs and smiles (toothlessly) at everything he sees. When he's especially pleased, he goes into a 'full body smile', in which he throws back his head, waves his arms, and kicks up his legs. </p>\n<p>He's always slept well during the night, 4 hours at a stretch, but the last two nights he's slept through from 11pm to 5am. We're hoping that's a new trend.</p>","fields":{"title":"Christopher at 11 Weeks","date":"12 Mar 2000","url":"https://papascott.de/2000-03-12-christopher-at-11-weeks/"}}},"pathContext":{"slug":"/2000-03-12-christopher-at-11-weeks/","prev":{"fields":{"title":"Hippo Day","date":"11 Mar 2000","slug":"/2000-03-11-hippo-day/"}},"next":{"fields":{"title":"Why are they doing road repair on both my routes to work?!?","date":"13 Mar 2000","slug":"/2000-03-13-why-are-they-doing-road-repair-on-both-my-routes-to-work/"}}}}

